import * as React from 'react';
import { styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import HomeIcon from '@mui/icons-material/Home';
import { Avatar } from '@mui/material';


import { Link, Navigate, Outlet, redirect } from 'react-router-dom';
import { checkAuthenticated, loadUser, login_failed } from '../features/userSlice'
import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useState } from 'react';

const drawerWidth = 240;

const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: 'hidden',
});

const closedMixin = (theme) => ({
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: 'hidden',
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up('sm')]: {
    width: `calc(${theme.spacing(8)} + 1px)`,
  },
});

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
    color: 'transparent',
    // sx: { backgroundColor: "white" }
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
      color: 'transparent',

    }),
  }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
    boxSizing: 'unset',
    ...(open && {
      ...openedMixin(theme),
      '& .MuiDrawer-paper': openedMixin(theme),
    }),
    ...(!open && {
      ...closedMixin(theme),
      '& .MuiDrawer-paper': closedMixin(theme),
    }),
  }),
);

export default function MiniDrawer() {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const dispach = useDispatch()
  const [loading, setLoading] = useState(true)






  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };


  // Handle Authentication
  useEffect(() => {
    dispach(checkAuthenticated())
    dispach(loadUser())




  }, [])

  // Loading Page
  const user = useSelector(state => state.user.user)
  const isAuth = useSelector(state => state.user.isAuthenticated)



  if (isAuth === "NA") {
    return (
    <div className=' w-[100%] h-[700px] flex  justify-center  items-center '>
      <img className='  w-[50%] m-auto  p-20 shadow-sm shadow-blue-100' src='https://www.sirmametal.com/wp-content/uploads/2019/12/sirmalogo.png' />
    </div>)

  } else {







      // must be "/auth/login" not "auth/login"
    return !isAuth === true ? <Navigate to="/auth/login"  /> : (
      <Box sx={{ display: 'flex' }}>
        <CssBaseline />

        {/* costumize appbar color */}
        <AppBar sx={{ background: 'white', boxShadow: 2 }} position="fixed" open={open}>
          <Toolbar className='flex justify-between'>
            <div className='flex'>
              <IconButton
                color="black"
                aria-label="open drawer"
                onClick={handleDrawerOpen}
                edge="start"
                sx={{
                  marginRight: 5,
                  ...(open && { display: 'none' }),
                }}
              >
                <MenuIcon />
              </IconButton>
              {!open && <img className=' w-[20%]' loading='layz' src='https://www.sirmametal.com/wp-content/uploads/2019/12/sirmalogo.png' />
              }
            </div>


            {/* User And Avatar */}

            <div className='flex  flex-col justify-center items-center p-2'>
            <Avatar />
            <Typography className='text-black font-bold text-center'>{user?.name}</Typography>
            </div>


          </Toolbar>

        </AppBar>

        <Drawer variant="permanent" anchor="left" open={open}>
          <DrawerHeader>

            {/* Here the arrow for opening and closing */}
            <div className='flex justify-between'>
              <IconButton onClick={handleDrawerClose}>
                {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
              </IconButton>

              <img className='flex-1 w-[40%] m-2' src='https://www.sirmametal.com/wp-content/uploads/2019/12/sirmalogo.png' />
            </div>

          </DrawerHeader>




          <List >
            {[{ name: "All Companies", link: "/" }, { name: "Home", link: "/analytics" }].map((item, index) => (
              <Link to={item.link}>
                <ListItem key={item.name} disablePadding sx={{ display: 'block' }}>
                  <ListItemButton
                    sx={{
                      minHeight: 48,
                      justifyContent: open ? 'initial' : 'center',
                      px: 2.5,
                    }}
                  >
                    <ListItemIcon
                      sx={{
                        minWidth: 0,
                        mr: open ? 3 : 'auto',
                        justifyContent: 'center',
                      }}
                    >
                      {index % 2 === 0 ? <HomeIcon /> : <HomeIcon />}
                    </ListItemIcon>
                    <ListItemText primary={item.name} sx={{ opacity: open ? 1 : 0 }} />

                  </ListItemButton>
                </ListItem>
              </Link>
            ))}
          </List>
          <List>
            {['login', 'signup', 'logout'].map((text, index) => (
              <Link key={text} to={text}>
                <ListItem key={text} disablePadding sx={{ display: 'block' }}>
                  <ListItemButton
                    sx={{
                      minHeight: 48,
                      justifyContent: open ? 'initial' : 'center',
                      px: 2.5,
                    }}
                  >
                    <ListItemIcon
                      sx={{
                        minWidth: 0,
                        mr: open ? 3 : 'auto',
                        justifyContent: 'center',
                      }}
                    >
                      {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
                    </ListItemIcon>
                    <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
                  </ListItemButton>
                </ListItem>
              </Link>
            ))}

          </List>
          {/* <div onClick={()=> dispach(login_failed())}> logout</div> */}
        </Drawer>
        <Box sx={{padding:5}} component="main">
          <DrawerHeader />
          <Outlet />
        </Box>
      </Box>
    );
  }
}

