import React, { useEffect, useState } from "react";
import axios from "axios";
import { useLoaderData } from "react-router-dom";

//Charts
import { RenderBarChart } from "../components/charts/barchart";
import { RenderLineChart } from "../components/charts/linechart";

import { Label, Treemap } from "recharts";
import { Button, Paper } from "@mui/material";
import { useSelector } from "react-redux";

export default function Analytics() {
  //states

  // analytics data
  const [data, setData] = useState({});

  // Louding
  const [isLoading, setIsLoading] = useState(true);

  // days to subtract
  const [daysToSubtract, setdaysToSubtract] = useState(1);

  //redux jwt
  const jwt = useSelector((state) => state.user.access);

  // use Effect when page is loaded
  useEffect(() => {
    // define url
    const url =
      "http://127.0.0.1:8000/api/companies/analitics/" + daysToSubtract;
    console.log(url);

    // get data from server
    axios
      .get(url, { headers: { Authorization: `JWT ${jwt}` } })
      .then((res) => {
        console.log(res.data);
        setData(res.data);
      })
      .catch((err) =>
        alert("Error when fetching Analytics data " + err.response.data)
      );

    // set loading to false
    setIsLoading(false);
  }, [daysToSubtract]);

  return isLoading ? (
    <div className=" w-[100%] h-[700px] flex flex-column justify-center  items-center ">
      <img
        className="  w-[50%] m-auto  p-20 shadow-sm shadow-blue-100"
        src="https://www.sirmametal.com/wp-content/uploads/2019/12/sirmalogo.png"
      />
    </div>
  ) : (
    <div className="text-3xl items-center justify-center">
      <Paper>
        <h1 className="text-center ">Aalytics</h1>
        <h1 className="text-left">

        <a href="tel:+36 20 458 4906">Call me!</a>

          {" "}
          Total Nummber Of Companies : {data.NumOfCompanies}
        </h1>

        <Button onClick={() => setdaysToSubtract(1)}>Today</Button>
        <Button
          onClick={() => {
            setdaysToSubtract(7);
            console.log("clicked");
            console.log(daysToSubtract);
          }}
        >
          last week
        </Button>
        <Button onClick={() => setdaysToSubtract(31)}>last month</Button>
        <Button onClick={() => setdaysToSubtract(90)}>last 3 monthes</Button>
        <Button onClick={() => setdaysToSubtract(0)}>All Time</Button>


        <RenderBarChart
          data={data.CountryToConmpny}
          labels={"country"}
          values={"company_count"}
          w={700}
        />
        <RenderBarChart
          data={data.ContactTypeCount}
          labels={"contact_type"}
          values={"contact_count"}
          w={700}
        />
        <RenderBarChart
          data={data.ContactResultCount}
          labels={"result"}
          values={"result_count"}
          w={900}
        />

        <RenderLineChart
          data={data.ContactCountDataSeries}
          labels={"Date"}
          values={"Number Of Contacts"}
          w={900}
        />

       
      </Paper>
    </div>
  );
}
